        .brew-ring-series__header-content
          .brew-ring-series__subject
            h1 精選求婚戒｜私釀求婚戒
          .brew-ring-series__description
            p 獨家設計精選，運用不同工藝技法製作，讓婚戒擁有各種光亮面和磨砂面混合交織的獨特美感，各款式皆各有亮點。
            | 私釀婚戒訂製，可依新人們的喜好調整：戒指寬度，鑽石數量，及金屬成色等，讓婚戒充滿個人風格及質感，也是分享給新人的婚戒新選擇
    b-row(:no-gutters="true")
      b-col.brew-ring-series__image-wrapper(
        v-for="(image, idx) in images" :key="image.alias"
        @click="clickHandler(idx)"
        cols="12" md="4")
        img.brew-ring-series__image-item(
          :src="image.path[0]"
          :alt="image.alias")
    .brew-ring-series__modal(v-if="isModalOpened")
      .brew-ring-series__modal-wrapper
        .brew-ring-series__modal-close(@click="isModalOpened = false")
        carousel.carousell(
          :per-page="1"
          :mouse-drag="true"
          :autoplay="true"
          :centerMode="true"
          :loop="true"
          :autoplayTimeout="3000"
          :paginationPosition="'bottom-overlay'"
        )
          slide(v-for="(img, idx) in images[modalRingIndex].path" :key="idx")
            img(
              :src="img"
              @click.stop="modalClickHandler"
            )
</template>
<script>
import brewRingInfo from '@/views/BrewRingSeries/brewRingSeriesInfo';
import { Carousel, Slide } from 'vue-carousel';

export default {
  data() {
    return {
      isModalOpened: false,
      modalRingIndex: 0,
    };
  },
  name: 'brewRingSeries',
  components: {
    Carousel,
    Slide,
  },
  methods: {
    modalClickHandler() {
      console.log('click on image');
    },
    clickHandler(index) {
      this.modalRingIndex = index;
      this.isModalOpened = true;
    },
  },
  computed: {
    brewRingSeries() {
      // return brewRingInfo[this.$route.params.type];
      return brewRingInfo.engage;
    },
    type() {
      return this.$route.params.type
        ? `brew-ring-series__banner--${this.$route.params.type}`
        : '';
    },
    title() {
      return this.brewRingSeries.title;
    },
    images() {
      return this.brewRingSeries.images;
    },
    description() {
      return this.brewRingSeries.description;
    },
  },
  deactivated() {
    this.isModalOpened = false;
  },
};

</script>

